<template>
    <div>
		<input type="text" ref="input" :value="value" @input="doThis"  style="display:none"/>
		<el-button class="gcategory-btn" type="text" @click="showDialogTree()">{{selectTip}}&nbsp;></el-button>
		<div class="departmentLists" v-if="checkedTreeItems && checkedTreeItems.length > 0">
			<b>已选：</b>
			<span v-for="(item, index) in checkedTreeItems" :key="index" :class="index==0 ? 'first' :''">
				，{{item}}
			</span>
		</div>
		<!--选择销售部门弹窗 start-->
		<el-dialog class="gcategory-dialog" title="销售分部" :visible.sync="dialogTree">
			<div class="el-dialog__tree">
				<el-tree
				ref="tree"
				class="tree-group__item"
				:data="treeItems"
				show-checkbox
				:props="defaultProps"
				node-key="id"
				disabled="true"
				:default-expanded-keys="[1, 2]"
				:default-checked-keys="checkedTreeKeys"
				>
				</el-tree>
			</div>
			<span slot="footer" class="dialog-footer">
				<el-button size="mini" type="primary" @click="hideDialogTree">确 定</el-button>
			</span>
		</el-dialog>
    <!--选择销售部门弹窗 end-->
    </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'g-division',
    props:{
	  value: {
        required: true,
      }
    },
    data () {
      return {
		//isDirector:this.value.isDirector,//是否是主任 true:是 ；false：否
		storeCodes:this.value.storeCodes,//一级品类id
		selectTip:"请选择(默认全选)",
        radio1:3,
        form: {
          childStoreCodes: [],
        },
        treeItems: [],
		checkedTreeItems: [],
        checkedTreeKeys: [1],
        defaultProps: {
          label: 'name',
          children: 'children'
        },
        dialogTree: false,
		branchList:[],	
      }
    },
    created(){
      // 配置环境变量
      var  curhost = location.host;
      this.url = "//"+curhost+"/pcorderview";
	  
  },
    mounted () {
		//初始化数据查询品类
      	this.__queryHierarchy();
    },
    watch: {
		'value':{
			handler(a){
				let that=this;
				if(a.storeCodes =="reset"){
					that.storeCodes=[];
					that.checkedTreeItems=[];
					this.setCheckedKeys();//树形结构恢复默认全选
					that.doThis();
					
				};
           		
			},
			deep: true 
      	} 
    },
    methods: {
		doThis(){
			let  that = this;
        	this.$emit('input', {
				storeCodes:this.storeCodes});
		},
      //查询销售部门
      __queryHierarchy() {
        let _this = this;
        //点击弹窗发送请求获取销售部门数据，发送一次请求即可
        //axios.post(_this.url+'/queryUserMultiCateList').then(response => {
		axios.post(_this.url+'/queryHierarchyNew').then(response => {
		console.log(response)
          if (response.data.success) {

            const _res = response.data.response;
            _res.list.forEach( (item) =>{//将一级标记出来
			  item.level="1";
			  item.disabled=false;
            });
            this.branchList =_res.list;
            this.treeItems = [{
              name: '全部选择',
              id: 1,
              children: this.branchList
			}];
          }
        });
      },
      //获取选择的部门
      getCheckedNodes() {
        //每次打开弹窗先将门店数组置空，选择了门店信息再赋值，避免选择了门店会重复向childStoreCodes添加id的问题
        this.form.childStoreCodes = [];
      //  this.form.category2Code = [];
        let nodes = [];

        //如果存在只选一个二级品类的时候，将对应的一级品类也返回
        
        if (this.$refs.tree !== undefined) {
          nodes = this.$refs.tree.getCheckedNodes();
        }
        let oThis = this;
        let checkedNodes = [];
       
        nodes.forEach((v) => {
            if (v.storeId && v.storeId != null) {
              oThis.form.childStoreCodes.push(v.storeId);
            }
			if (v.storeName && v.storeName != undefined) {
				checkedNodes.push(v.storeName)
			}
        });
        //将重复的部门过滤
        this.checkedTreeItems = checkedNodes.filter(function(element,index,self){
          return self.indexOf(element) === index;
		});
		// if(checkedNodes.length== 0){//什么都不选
		// 	this.selectTip = "请选择";
		// }else{
		// 	this.selectTip = "请选择(默认全选)";
		// };
      },

      //显示销售部门树型菜单
      showDialogTree () {
        this.dialogTree = true;
      },

      //关闭销售属性树形菜单
      hideDialogTree () {
        this.dialogTree = false;
        this.getCheckedNodes();
        //将获取到的id传给父组件，给查询时使用
        let data = {
          storeCodes: this.form.childStoreCodes,
         // category2Code:this.form.category2Code.join(',')
		};
		
		this.$emit('changeend',data);
		this.$emit('input', {storeCodes:data.storeCodes});
      },
      //重置操作之后，通过key设置默认选中项
      setCheckedKeys() {
        let _this = this;
        this.$nextTick(() => {
          _this.$refs.tree && _this.$refs.tree.setCheckedKeys([1]);
        });
	  },
    }
  }
</script>



